p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

img {
  display: block;
  max-width: 100%;
  width: 100%;
  height: auto;
}

.link {
  text-decoration: none;
}
.list {
  text-decoration: none;
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
